import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { ApiService } from '@services';
import { ENDPOINTS } from '@shared';
import { Announcement } from 'app/core/models/announcement.model';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-announcement',
  templateUrl: './announcement.component.html',
  styleUrls: ['./announcement.component.scss']
})
export class AnnouncementComponent implements OnInit {
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  dataSource: MatTableDataSource<Announcement>;
  announcement = new Announcement();
  announcementList = [];
  announcementListFilter = [];
  viewCounter: number = 0;
  constructor(
    private apiService: ApiService,
    private spinner: NgxSpinnerService
  ) { }

  ngOnInit(): void {
    this.getAnnouncementList();
  }

  getAnnouncementList(): void {
    this.spinner.show();
    this.apiService.findAll(ENDPOINTS.activeAnnouncements).subscribe(
      (res: any) => {
        if (res.data) {
          this.announcement = res.data;
          this.announcementList = res.data;
          this.announcementListFilter =  this.announcementList.slice(0*5, 0*5 + 5);
          setTimeout(() => {
            this.spinner.hide();
          }, 1);
        }
      }
    )
  }

  countView(data): void{
    this.announcement = data;
    data.counter++;
    this.apiService.update(ENDPOINTS.announcements, this.announcement).subscribe(
      (res:any) => {});
  }

  onPageChange($event) {
    this.announcementListFilter =  this.announcementList.slice($event.pageIndex*$event.pageSize, $event.pageIndex*$event.pageSize + $event.pageSize);
  }

}
